import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private http: HttpClient) { }

  getStatistics() {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/statistics/numbers';
    return this.http.get<any>(url);
  }

  getCategoryPerProduct() {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/statistics/category-products';
    return this.http.get<any>(url);
  }

  getStoreType() {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/statistics/store-types';
    return this.http.get<any>(url);
  }

  getPopularProducts() {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/statistics/popular-products';
    return this.http.get<any>(url);
  }

  getPopularStores() {
    const url: string = environment.MAIN_SYSTEM + 'api/dashboard/statistics/popular-stores';
    return this.http.get<any>(url);
  }
}
