import { Component, OnInit } from '@angular/core';
import { ProgressInfo } from '../../@core/data/stats-progress-bar';
import { DashboardService } from '../../services/dashboard/dashboard.service';


@Component({
  selector: 'app-dashboard',
  templateUrl: 'dashboard.component.html',
  styleUrls: ['dashboard.component.scss'],
})
export class DashboardComponent implements OnInit {
  public statistics: any;
  public CategoryProductNumbers: any[];
  public productCategoryXData: any;
  public productCategoryYData: any;
  public productCategoryPieData: any;
  public categoryloading: boolean;

  public typeXData: any;
  public typeYData: any;
  public typeloading: any;
  public typePieData: any;

  public productPopularXData: any;
  public productPopularYData: any;
  public storePopularXData: any;
  public storePopularYData: any;

  public dashboardStatisticsList: any = [
    {
      name: 'admins',
      color: 'primary',
      icon: 'people-outline'
    },
    {
      name: 'orders',
      color: 'info',
      icon: 'shopping-cart-outline'
    },
    {
      name: "products",
      color: 'success',
      icon: 'shopping-bag-outline'
    },
    {
      name: "sellers",
      color: 'warning',
      icon: 'person-done-outline'
    },
  ]
  constructor(private _DashboardService: DashboardService) {
  }

  ngOnInit(): void {

    this.getStatistics();
    this.getCategoryProduct();
    this.getStoreType();
    this.getPopularProduct();
    this.getPopularStore();
  }

  progressInfoData: ProgressInfo[];

  getStatistics() {
    this._DashboardService.getStatistics().subscribe(
      res => {
        this.statistics = res.data;
      },
      error => { },
      () => { },
    )
  }

  getCategoryProduct() {
    this.categoryloading = true;
    this._DashboardService.getCategoryPerProduct().subscribe(
      res => {
        this.productCategoryXData = res.data.map(ele => ele.category.name_en)
        this.productCategoryYData = res.data.map(ele => ele.products);
        this.productCategoryPieData = res.data.map(ele => { return { name: ele.category.name_en, value: ele.products } });
      },
      error => {
        this.categoryloading = false;
      },
      () => {
        this.categoryloading = false;
      },
    );
  }

  getStoreType() {
    this.typeloading = true;
    this._DashboardService.getStoreType().subscribe(
      res => {
        this.typeXData = res.data.map(ele => ele.name_en)
        this.typeYData = res.data.map(ele => ele.count);
        this.typePieData = res.data.map(ele => { return { name: ele.name_en, value: ele.count } })
      },
      error => {
        this.typeloading = false;
      },
      () => {
        this.typeloading = false;
      },
    );
  }

  getPopularProduct() {
    this._DashboardService.getPopularProducts().subscribe(
      res => {
        this.productPopularXData = res.data.map(ele => ele.name);
        this.productPopularYData = res.data.map(ele => ele.count);
      },
      error => { },
      () => { },
    )
  }

  getPopularStore() {
    this._DashboardService.getPopularStores().subscribe(
      res => {
        this.storePopularXData = res.data.map(ele => ele.name);
        this.storePopularYData = res.data.map(ele => ele.count);
      },
      error => { },
      () => { },
    )
  }


  IsObject(value: any) {
    return typeof value === 'object' ? true : false;
  }

}
