import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
@Component({
  selector: 'product-statistics',
  templateUrl: './product-statistics.component.html',
  styleUrls: ['./product-statistics.component.scss']
})
export class ProductStatisticsComponent implements OnInit {
  @Input() public xAxisData: any;
  @Input() public data: any;
  @Input() header: string;
  @Input() loading: boolean;
  @Input() type: string;
  @Input() dataName: string;
  @Input() legend: string[] = [];
  public options: any;

  constructor() { }

  ngOnInit(): void {
    // const xAxisData = [];
    // const data1 = [];
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.xAxisData && changes.xAxisData.currentValue) {
      this.options = {
        legend: {
          data: this.legend,
          x: 'center',
          y: 'bottom',
        },
        tooltip: {},
        xAxis: {
          data: this.type === 'bar' ? this.xAxisData : [],
          silent: false,
          splitLine: this.type === 'bar' ? {
            show: false,
          } : {},
        },
        yAxis: {},
        series: [
          {
            name: this.dataName,
            type: this.type,
            data: this.data,
            color: ['#0095ff', '#f9a600'],
            animationDelay: (idx) => idx * 10,
          }
        ],
        animationEasing: 'elasticOut',
        animationDelayUpdate: (idx) => idx * 5,
      };

      if (this.type !== 'bar') {
        delete this.options.xAxis;
        delete this.options.yAxis;
      }
    }
  }

}
